* {
  margin: 0;
  padding: 0;
  list-style: none;
}
.debug {
  background-color: #fae;
  border: solid 3px rgb(255, 0, 0);
}
nav {
  display: flex;
  flex-wrap: nowrap;
  background-color: #000;
}
/* nav left */
nav:nth-child(0) {
  flex: 1;
  justify-content: flex-start;
}
/* nav middle */
nav:nth-child(1) {
  flex: 2;
  justify-content: space-between;
}
/* nav right */
nav:nth-child(2) {
  flex: 1;
  justify-content: flex-end;
}
.navbox {
  display: flex;
  flex-wrap: wrap;
}
.navitem {
  height: 40px;
  line-height: 40px;
  padding: 6px 15px;
  color: #fff;
  font-size: 16px;
  text-decoration: none;

  /* 僅"初始"被景色為繼承的顏色 */
  background-color: initial ; 
  transition: 0.5s;
}

.navitem:hover {
  background-color: #fa0;
}
