.channellist {
  width: 100%;
  max-width: 1400px;
  margin: auto;
}
/* .channel_item {
  background-color: #ffa;
  margin: 0px 10px 20px 10px;
  padding: 10px;
  flex-basis: 15rem;
} */
.previewbox {
  display: block;
  background-color: #f00;
  align-items: center;
}
.previewbox img {
  width: 100%;
  vertical-align: middle;
}

@media screen and (min-width: 768px) {
  .channel_list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: auto;
  }
  .previewbox {
    height: 11.25rem;
    background-color: #000;
    display: flex;
    align-items: center;
  }
  .previewbox img {
    width: 100%;
    vertical-align: bottom;
  }
}

.avatarbox {
  position: absolute;
}

.channel_Description {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  /* background-color: #f00; */
  /* justify-content: center;
  align-items: center;  */
  text-align: center;
}

.channel_Title {
  height: 54px;
  /* background-color: #fff; */
  line-height: 18px;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 5px 10px 5px;
  font-size: 16px;
}
.channel_Owner {
  height: 50px;
  line-height: 50px;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0 10px 5px;
  font-size: 16px;
  /* background-color: #fff; */
}

.channel_Views {
  height: 22px;
  line-height: 22px;
  margin: 0 10px 5px;
  font-size: 16px;
  /* background-color: #fff; */
}

.channel_Time {
  height: 18px;
  line-height: 18px;
  margin: 0 10px 5px;
  font-size: 14px;
  /* background-color: #fff; */
}
