.searchbox {
  margin: 0 0 20px;
  padding: 5px 0 0;
  width: 100%;
  background-color: #000;
  display: flex;
  align-items: flex-end;
}

.searchInput {
  flex: 1;
  margin: 0 0px 5px;
  padding: 1rem;
  border: solid 2px #686868;
  border-radius: 5px;
  margin-right: 5px;
  outline: none;
}
.searchInput:focus {
  border-color: #005ad8;
}

.tagsButton {
  width: 4rem;
  margin: 0 2px 5px;
  padding: 1rem;
  border: solid 2px rgb(14, 172, 61);
  border-radius: 5px;
  background-color: rgb(44, 146, 24);
  color: white;
  outline: none;
  cursor: pointer;
  display: block;
}
@media screen and (max-width: 768px) {
    .tagsButton {
        display: none;
    }
}

.searchButton {
  width: 4rem;
  margin: 0 2px 5px;
  padding: 1rem;
  border: solid 2px #0a1627;
  border-radius: 5px;
  background-color: #005ad8;
  color: white;
  outline: none;
  cursor: pointer;
}
